  .brand-page__filter_row {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  .brand-page__wrapper {
    margin: 30px 0 0;
    gap: 30px;
  }
  .brand-page__item_name {
    font-size: 16px;
  }
  .brand-page__item_text {
    font-size: 13px;
  }
  .brand-page__item_img img {
    height: 75px;
  }
  .pag {
    gap: 0;
    row-gap: 15px;
  }
  .pag__next {
    margin: 0 0 0 auto;
  }
  .pag ul {
    gap: 0;
  }
  .product h1 {
    font-size: 23px;
  }
  .product__wrapper {
    margin: 30px 0 0;
  }
  .product__name {
    grid-template-columns: 55px 1fr;
    gap: 5px;
  }
  .product__name span {
    width: 55px;
    height: 55px;
  }
  .product__name svg,
  .product__name img {
    width: 30px;
    height: 30px;
  }
  .product__name p {
    font-size: 18px;
  }
  .form h1 {
    font-size: 23px;
  }
  .form__wrapper {
    margin: 20px 0 0;
    padding: 20px;
  }
  .form__text {
    font-size: 12px;
  }
  .form__input input,
  .form__input textarea {
    padding: 15px;
    font-size: 14px;
  }
  .form__input p {
    font-size: 15px;
  }
  .form__input textarea {
    height: 100px;
  }
  .form__file_text {
    font-size: 15px;
  }
  .supply h1 {
    font-size: 23px;
  }
  .brand-item__img {
    width: 100%;
  }
}